import OverMijGif from '../images/OverMij/OverMij.gif';
import Wowbeams from '../images/OverMij/wowbeamb.png';
import OverMijStatic from '../images/OverMij/overmijskills.png';

import Skillmeter from "../components/Skillmeter";
import { SkillsItems } from '../components/SkillsItems';

import "../components/uniquecss.css";
import { OMButton, Active, InActive }from "../components/PtButton";


import { gsap } from "gsap";
import { useState, useEffect, useRef } from 'react';

function Vaardigheden() {
  const OverMijPg = useRef();
  const [selectedSkill, setSelectedSkill] = useState(0);
  const [imgGone, setImgGone] = useState(false);

  function EntryAnimation(){
    let ctx = gsap.context(() => {
      gsap.fromTo(".Overmijimg", {opacity: 1,y:-1000, scaleX: 0.2, scaleY:1.8},{y:0, scaleX:1, scaleY:1, duration:.2});
      gsap.fromTo(".Overmijimg", {y:0, scaleX:1, scaleY:1, },{y:0, scaleX:1.3, scaleY:0.7, duration:.1, delay:.2});
      gsap.fromTo(".Overmijimg", {y:0, scaleX:1.3, scaleY:0.7, },{y:0, scaleX:0.9, scaleY:1.1, duration:.1, delay:.3});
      gsap.fromTo(".Overmijimg", {y:0, scaleX:0.9, scaleY:1.1, },{y:0, scaleX:1, scaleY:1, duration:.1, delay:.3});
      gsap.fromTo(".wowbeam", {opacity:0},{opacity:1, delay:.4});
      gsap.fromTo(".keuzemenu", {opacity:0, y:-10},{opacity:1, y:0, stagger:0.15, delay:.4});
    }, OverMijPg); 
  }

  function ExitAnimation(){
    let ctx2 = gsap.context(() => {
      gsap.fromTo(".wowbeam", {opacity:1},{opacity:0, delay:0, duration: 0.05});
      gsap.fromTo(".Overmijimg", {y:0, scaleX:1, scaleY:1 },{y:0, scaleX:1.4, scaleY:0.6, duration:.1, delay:.05});
      gsap.fromTo(".Overmijimg", {y:0, opacity:1 },{y:-200, scaleX:0.5, scaleY:1.5, duration:.1, delay:.3, opacity: 0, display:'none'});
      gsap.fromTo(".wowbeam", {opacity:0},{display:'none', delay:.3});
    }, OverMijPg); 
    setImgGone(true);
  }

  function mdScreenShow(){
    gsap.fromTo(".Skill", {opacity:0},{ display:'block',opacity:1, delay:.3, duration: 0.2});
  }

  function slideInSkills(){
    let ctx2 = gsap.context(() => {
      gsap.fromTo(".Skill", {opacity:0},{ display:'block',opacity:1, delay:.3, duration: 0.2});
    }, OverMijPg); 
  }

  useEffect(()=>{
    if(!imgGone){
      EntryAnimation();
      if (window.innerWidth >= 768){
        console.log(window.innerWidth);
        mdScreenShow();
        
      }
    }
  })

  function filterSkill(syskey){
    if(!imgGone){
      window.innerWidth <= 768 ? ExitAnimation() : setImgGone(true);

    }
    setSelectedSkill(syskey);
    slideInSkills();
  }

  return(
    <div ref={OverMijPg} className="Vaardigheden bg-waves h-screen w-screen h-min-screen overflow-x-hidden xl:h-screen md:h-screen md:overflow-y-hidden">
      <div className="w-screen p-4">
        {/* Menu */}
        <h1 className="keuzemenu text-right text-2xl font-bold text-dark2 pt-12 sm:pt-4 md:text-3xl md:text-center lg:pt-10 lg:text-4xl lg:text-left font-handwrite">Mijn vaardigheden</h1>
      </div>

      <div className="md:grid md:grid-cols-2">
        <div> 
          <div className="FilterMenu grid grid-cols-3 z-10
            md:w-1/2 md:right-0 md:absolute">
            { SkillsItems.map((systaal, syskey)=>
              <div className="" key={syskey}>
                <OMButton><p style={setSelectedSkill == syskey ? Active : InActive} onClick={()=>{filterSkill(syskey)}}>{systaal.Taal}</p></OMButton>
              </div>
            )}
          </div>

          <div className="Skill opacity-0 md:-mt-8 md:h-2/3">
            <div className="bg-light rounded-lg w-11/12 h-fit mx-auto p-3 mt-10 bt-10 
              md:overflow-y-hidden">
              <div className="w-5/6 mx-auto divide-y divide-dark2"> 
                { SkillsItems[selectedSkill].Frameworks.map((systaal, syskey)=>
                <div className="" key={syskey}>
                    <div className="grid grid-cols-2 gap-0 mx-auto h-24 md:h-20 mb-2 flex items-stretch relative" >
                      <div className="SkillNaam text-2xl font-bold text-dark1 self-center"> <p>{systaal.Skillnaam}</p> </div>
                      <div className="SkillConfidence self-center text-right font-bold text-dark1"><Skillmeter level={systaal.Confidence}/></div>
                    </div>
                </div>
                )}
              </div>
            
            </div>
          </div>
        </div>

        <div className="Image w-screen bottom-0 absolute overflow-x-hidden overflow-y-hidden z-0 sm:h-2/3 md:w-full xl:w-3/4 xl:mx-auto
          md:static md:mt-72 md:rounded-full
          lg:mt-48">
          <div className="relative w-fit mx-auto z-0">
            <img className="Overmijimg z-10 absolute w-full" src={OverMijStatic}/>
            <img className="wowbeam bottom-12" src={Wowbeams}/> 
          </div>
        </div>
      </div>
    </div>
  )
}

export default Vaardigheden;