import styled from "styled-components";

import WaveLight from '../images/background/lightwave.svg';
import WaveDark from '../images/background/darkwave.svg';
const BackgroundWaveLight = styled.div`
background-image: url(${WaveLight});
background-repeat: repeat-x;
width: 100vw;
height: 100vh;
background-position: bottom;
bottom:0;
position: absolute;
zIndex:-800;
`

const BackgroundWaveDark = styled.div`
background-image: url(${WaveDark});
background-repeat: repeat-x;
width: 100vw;
height: 100vh;
background-position: bottom;
bottom:0;
position: absolute;
zIndex:-1000;
`

function Background({children}) {
  return(
    <div className="z-200">
      <BackgroundWaveDark>
        <BackgroundWaveLight>
          {children}
        </BackgroundWaveLight>
      </BackgroundWaveDark>
    </div>
  )
}

export default Background;