//Works
import frame1 from '../images/homepage/homepageGif2.gif';
import frame2 from '../images/portfolio/relax.gif';
import { useState, useEffect } from 'react';

function Animation(props){
  const[gif, setGif]=useState(frame1);

  switch(props.toPlay){
    case "welcome":
      setGif(frame1);
      break;
    case "Portfolio":
      setGif(frame2);
      break;
  }

  const reloadGif = () => { 
    setTimeout(() => {
      setGif(frame1)
    }, 0)
  }

  useEffect( () => {
    setGif('');
    reloadGif();
  }, []);

  return(
    <div className="Animation">
      <img src={gif}/>
    </div>
  )
}

