import { useState, useEffect } from 'react';

function Skillmeter(props){
  let [ sklevel, setSkLevel] = useState(props.level);
  let [ lvlNaam, setLvlNaam] = useState("Basis");
 
  if(props.level != sklevel){
    setSkLevel(props.level);
    switch (sklevel){
      case 1:
        setLvlNaam("Basis");
      break;
      case 2:
        setLvlNaam("Ervaren")
      break;
      case 3:
        setLvlNaam("Bekwaam")
      break;
      default:
        console.log("Ho");
      break;
    }
  }
  
  useEffect(()=>{
    switch (sklevel){
      case 1:
        setLvlNaam("Basis");
      break;
      case 2:
        setLvlNaam("Ervaren")
      break;
      case 3:
        setLvlNaam("Bekwaam")
      break;
      default:
        console.log("Ho");
      break;
    }
  }, [sklevel]);

  return(
    <div className="Skillmeter">
      <h1>{ lvlNaam }</h1>
      <i className="fa-solid fa-circle" style={{color: sklevel >= 1 ? '#E07AB7' : '#404356'}}/>
      <i className="fa-solid fa-circle" style={{color: sklevel >= 2 ? '#E07AB7' : '#404356'}}/>
      <i className="fa-solid fa-circle" style={{color: sklevel >= 3 ? '#E07AB7' : '#404356'}}/>
    </div>
  )
}

export default Skillmeter;