import OverMijImg from '../images/OverMij2/overmij.png';
import PixelartImg from '../images/OverMij2/pixelartExample.png';

import { gsap } from "gsap";
import { OMButton, Active, InActive }from "../components/PtButton";
import { useState, useEffect, useRef } from 'react';
import Scroll from 'react-scroll';

function OverMij() {
  const OverMijPg = useRef();
  var scroller2 = Scroll.scroller;

  const [contentPg, setContentPg] = useState(1);
  //Page enter
  function PageEnter(){
    gsap.fromTo(".Content", {opacity: 0, y:-100},{opacity:1, y:0, scaleX:1, scaleY:1, duration:.2});
    gsap.fromTo(".OverMijImg", {opacity: 0, x:+100},{x:0, opacity:1, duration: 0.2});
    gsap.fromTo(".OverMijImg2", {opacity: 0, x:+100},{x:0, opacity:1, duration: 0.2});
    
  }

  function changeContent(button){
    changeContentOut();
    setTimeout(()=>{
      changeContentIn(button);
    }, 300)

    scroller2.scrollTo('scrollTarget',{
      duration: 300,
      smooth: true,
    });

  }

  function changeContentIn(button){
    if(button == 1 ){
      gsap.fromTo(".Kopje1", {opacity: 1, x:-1000 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje1", {opacity: 1, x:+10 },{ x:0, duration: .3, delay:.2});
      gsap.fromTo(".Tekst1", {opacity: 1, x:-1000,},{x:0,opacity:1, duration: 0.3, delay:0.07});
      setContentPg(1);
    }
    if(button == 2 ){
      gsap.fromTo(".Kopje2", {opacity: 1, x:-1000 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje2", {opacity: 1, x:+10 },{ x:0, duration: .3, delay:.2});
      gsap.fromTo(".Tekst2", {opacity: 1, x:-1000,},{x:0,opacity:1, duration: 0.3, delay:0.07});
      setContentPg(2);
    }
    if(button == 3 ){
      gsap.fromTo(".Kopje3", {opacity: 1, x:-1000 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje3", {opacity: 1, x:+10 },{ x:0, duration: .3, delay:.2});
      gsap.fromTo(".Tekst3", {opacity: 1, x:-1000,},{x:0,opacity:1, duration: 0.3, delay:0.07});
      setContentPg(3);
    }
  }

  function changeContentOut(button){
    if(contentPg == 1 ){
      gsap.fromTo(".Kopje1", {opacity: 1, x:0 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje1", {opacity: 1, x:0 },{ x:-1000, duration: .3, delay:.2});
      gsap.fromTo(".Tekst1", {opacity: 1, y:0},{x:-1000, opacity:1, duration: 0.3, delay:0.3});
    }
    if(contentPg == 2 ){
      gsap.fromTo(".Kopje2", {opacity: 1, x:0 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje2", {opacity: 1, x:0 },{ x:-1000, duration: .3, delay:.2});
      gsap.fromTo(".Tekst2", {opacity: 1, y:0},{x:-1000, opacity:1, duration: 0.3, delay:0.3});
    }
    if(contentPg == 3 ){
      gsap.fromTo(".Kopje3", {opacity: 1, x:0 },{ x:+10, duration: .15});
      gsap.fromTo(".Kopje3", {opacity: 1, x:0 },{ x:-1000, duration: .3, delay:.2});
      gsap.fromTo(".Tekst3", {opacity: 1, y:0},{x:-1000, opacity:1, duration: 0.3, delay:0.3});
    }
  }

  useEffect(()=>{
    PageEnter();
  },[]);

  return(
    <div ref={OverMijPg} className="OverMij h-screen lg:bg-waves bg-light bg-fixed w-screen overflow-x-hidden">
{/* 
      <div className="md:hidden sm:block lg:hidden xl:hidden text-center">SM</div>
      <div className="md:block sm:hidden lg:hidden xl:hidden text-center">MD</div>
      <div className="md:hidden sm:hidden lg:block xl:hidden text-center">LG</div>
      <div className="md:hidden sm:hidden lg:hidden xl:block text-center">XL</div> */}

      <div className="grid lg:grid-cols-2 lg:p-4 h-screen">
        <div className="Content bg-light rounded-lg pt-8 px-6">
          <h1 className="text-highlight2 lg:text-5xl text-4xl font-bold font-handwrite lg:text-center text-right">Over Mij</h1>
          
          {/* KORT OVER MIJ */}
          <div className="md:grid md:grid-cols-1">
            <div className="OverMijImg2 order-1 mx-auto rounded-full overflow-hidden bg-light2 h-[280px] aspect-square border-4 border-dashed border-highlight1 lg:hidden">
              <img src={OverMijImg}/>
            </div>
            <div className="order-2 bg-contrast p-2 rounded-xl w-11/12 mx-auto my-auto lg:hidden">
              <p className="text-dark2 text-lg md:text-2xl py-2 font-handwrite text-center font-bold">
                Ik ben Michelle en ik ben CMD-student aan de Hanzehogeschool Groningen.
              </p>
            </div>
            <div className="order-2 mt-1 bg-dark2 p-2 rounded-xl w-4/5 mx-auto my-auto lg:hidden">
            <p className="text-contrast text-2xl font-bold">
              Contact 
            </p>
            <div className="col cols-grid-2">
              <div>
                <p className="text-light font-semibold"><i className="fa-sharp text-light fa-solid fa-paper-plane pr-4"></i> m.l.brachwitz@st.hanze.nl</p>
              </div>
            </div>
          </div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 Buttons flex items-stretch relative w-fit mx-auto lg:pt-3">
            <div className="self-center m-1"> 
              <OMButton><p onClick={()=>{changeContent(1)}}>Wat bied ik?</p></OMButton>
            </div>
            <div className="self-center m-1"> 
              <OMButton><p onClick={()=>{changeContent(2)}}>Wat doe ik verder?</p></OMButton>
            </div>
            <div className="self-center m-1"> 
              <OMButton><p onClick={()=>{changeContent(3)}}>Wat wil ik?</p></OMButton>
            </div>
          </div>

          {/* WAT HEB IK TE BIEDEN */}
          <div className="bg-dark1 w-full" id="OverMijContent">
            <div className="relative">

              <div className="Bieden absolute top-0">
                <h1 className="Kopje1 text-highlight2 text-2xl font-bold font-handwrite text-center pt-5">Wat kan ik je bieden?</h1>
                <p className="Tekst1 text-dark1 text-1xl font-medium px-4 whitespace-pre-line pt-2">
                  Ik ga op zoek naar de beste manieren om problemen op te lossen met techniek, want de mogelijkheden van dit medium zijn nog lang niet uitgeput.
                  Problemen zijn kansen die benut kunnen worden!{'\n\n'} 

                  Ik zal zoveel mogelijk informatie inzamelen over het probleem met behulp van desk- en fieldresearch. 
                  Hieruit komen richtlijnen die bepalen hoe het uiteindelijke technische concept eruit gaat zien.{'\n\n'}
                  
                  Vervolgens zal ik deze ontwerpen uitwerken tot een bruikbaar prototype met een ontwikkelde front- en (gedeeltelijke) back-end.{'\n\n'}
                </p>
                <div id="scrollTarget"></div>
              </div>
              <div className="VrijeTijd absolute top-0">
                <h1 className="Kopje2 opacity-0 text-highlight2 text-2xl font-bold font-handwrite text-center pt-5">Vrije tijd</h1>
                <p className="Tekst2 opacity-0 text-dark1 text-1xl font-medium px-4 whitespace-pre-line pt-2">
                  Ik vind het ontzettend leuk om te tekenen en heb hierbij verschillende stijlen ontwikkeld. De laatste paar jaar maak ik voornamelijk pixelart. Hele animaties of spritesheets vind ik ontzettend leuk om te maken.
                  Ik verwerk ze dan in games die ik maak in GameMaker of Godot. Deze wil ik nog combineren met models die ik maak in Blender.
                  {'\n\n'}
                  <img src={PixelartImg} className="mx-auto"/>          
                </p>
                <div id="scrollTarget"></div>    
              </div>

              {/* HOBBY'S */}
              <div className="Fullstack absolute top-0">
                <h1 className="Kopje3  opacity-0 text-highlight2 text-2xl font-bold font-handwrite text-center pt-5">Wat wil ik? Fullstack!</h1>
                <p className="Tekst3  opacity-0 text-dark1 text-1xl font-medium px-4 whitespace-pre-line pt-2 pb-10">
                  Ik vind alles aan webdevelopment super interessant!{'\n'}
                  {'\n'}
                  Webdesign en front-end development geeft heel veel energie. Niet alleen aan mij of mensen waar ik mee samenwerk, maar ook docenten en cliënten.{'\n'}
                  {'\n'}
                  Mijn back-end vaardigheden verfijn ik ook in mijn vrije tijd met mijn hobby: gamedevelopment. 
                  Ook heb ik uit nieuwsgierigheid Ruby on Rails opgepikt, cursus gevolgd over Django en heb ik iemand geholpen met het analyseren van hersengolfdata met behulp van Python.
                  Ook had ik een passie project waarbij ik mijzelf wou uitdagen meer gebruik te maken van Firebase, axios en een MySQL database. 
                  Als laatste heb ik afgelopen jaar nog een poging gedaan om C# te leren.
                </p>
                <div id="scrollTarget"></div>
              </div>
            </div>
          </div>
        </div>

        <div className="md:h-fit lg:block hidden my-auto">
          <img className="OverMijImg md:h-fit md:w-2/5 mx-auto my-auto" src={OverMijImg}/>

          <div className="order-2 bg-contrast p-2 rounded-xl w-2/3 mx-auto my-auto lg:block hidden">
            <p className="text-dark2 text-lg md:text-2xl py-2 font-handwrite text-center font-bold">
              Ik ben Michelle en ik ben CMD-student aan de Hanzehogeschool Groningen.
            </p>
          </div>

          <div className="order-2 mt-5 bg-dark2 p-2 rounded-xl w-2/3 mx-auto my-auto lg:block hidden">
            <p className="text-contrast text-2xl font-bold">
              Contact 
            </p>
            <div className="col cols-grid-2">
              <div>
                <p className="text-light font-semibold"><i className="fa-sharp text-light fa-solid fa-paper-plane pr-4"></i> m.l.brachwitz@st.hanze.nl</p>
              </div>
            </div>
          </div>
        </div>


      </div>
    </div>
  )
}

export default OverMij;