//DONE
import styled from "styled-components";
import Scroll from 'react-scroll';
import { PtButton, Active, InActive }from "../components/PtButton";
import { gsap } from "gsap";
import { useState, useEffect, useRef } from 'react';
import "../components/uniquecss.css";
import frame2 from '../images/portfolio/portfolio2.gif';

//Hero images
import HeroCb from '../images/portfolio/HeroCargoBike.png';
import HeroAgave from '../images/portfolio/HeroAgave.png';
import HeroDigiStart from '../images/portfolio/HeroDigiStart.png';
import HeroKast from '../images/portfolio/HeroKastPasser.png';

//  Data
import { portfolioItems } from "../components/PortfolioItems.js";
import SwipeImg from '../images/portfolio/swipe.svg';

//BG Image
import backgroundimg from '../images/background/bg.png';
const Bgimg = styled.div`
  background-image: url(${backgroundimg});
  width: 100vw;

  z-index: -800;
  top:0;
  background-attachment: fixed;
  background-repeat: no-repeat;
`


//Page
function Portfolio() {
  //Scroller
  var scroller = Scroll.scroller;
  function PtNavigator(PtItem){
    PtButtonAnim();
    gsap.fromTo(".portImage", {opacity:1, y: 0},{y: -400, duration: .3, delay: 0, opacity: 0});
    gsap.fromTo("#PortContent", {opacity:1, y: 0},{duration: .3, delay: 0, opacity: 0});
    gsap.fromTo("#HeroImg", {opacity:1, y: 0},{duration: .3, delay: 0, opacity: 0});
    setTimeout(() => {
      setActiveItem(PtItem);
      gsap.fromTo(".portImage", {opacity:0, y: -400},{y: 0, duration: .2, stagger: .1, delay: .2, opacity: 1});
      gsap.fromTo("#HeroImg", {opacity:0, y: 0},{duration: .3, delay: 0, opacity: 1});
      gsap.fromTo("#PortContent", {opacity:0, y: 0},{duration: .3, delay: 0, opacity: 1});
    }, 500);

    scroller.scrollTo('PortContentScroll',{
      duration: 500,
      smooth: true,
    });
  }

  function PtButtonAnim(PtItem){
    gsap.fromTo("#btn0", {opacity:1, y: 0},{y: +10, duration: .2, delay: 0, opacity: 1});
    gsap.fromTo("#btn0", {opacity:1, y: +10},{y: 0, duration: .1, delay: .2, opacity: 1});
  }

  //Nav
  const [ activeItem, setActiveItem ] = useState(0);
  const heroImg = [HeroCb, HeroAgave, HeroDigiStart, HeroKast]

  portfolioItems[activeItem].album.map((image) => <img src={image}/>);

  //  Animation
  const portfolioRef = useRef();
  useEffect(() => {}, [activeItem]);

  return (
    <div  ref={portfolioRef}  className="Portfolio bg-waves">
      {/* <h1 className="md:block sm:hidden lg:hidden xl:hidden 2xl:hidden text-4xl text-center">MD</h1>
      <h1 className="lg:block sm:hidden md:hidden xl:hidden 2xl:hidden text-4xl text-center">LG</h1>
      <h1 className="lg:hidden sm:block md:hidden xl:hidden 2xl:hidden text-4xl text-center">SM</h1>
      <h1 className="lg:hidden sm:hidden md:hidden xl:block 2xl:hidden text-4xl text-center">XL</h1>
     */}
      {/* PAGE CONTAINER */}
      <div className="
        xl:grid xl:grid-cols-6 xl:h-screen xl:w-full">
          {/* KEUZEMENU MODAL GRID1*/}
          <div id="Index" className="h-screen w-screen pt-12 relative overflow-y-hidden bg-gradient-to-b to-light2 from-light 
            sm:h-fit sm:pb-10
            xl:w-full xl:col-span-1 xl:h-screen
          ">
              <div className="w-5/6 mx-auto 
                sm:w-full sm:grid sm:grid-cols-2
                xl:w-fit ">
                <div className="xl:grid xl:grid-cols-1 xl:ml-4">
                  <h1 className="text-3xl text-right xl:text-left font-black w-48 float-right mt-12 font-handwrite
                    sm:float-none sm:mx-auto sm:text-center
                    xl:text-4xl xl:ml-auto xl:text-center xl:mt-0">Wat zou je willen zien?</h1>
                  <div className="mt-2 grid grid-cols-2 gap-x-40 gap-y-3 pt-32
                    sm:float-none sm:pt-0 sm:grid-cols-1
                    xl:grid-cols-1 xl:mx-auto xl:mt-0 xl:pt-5 xl:gap-3">
                  
                    <PtButton> <p style={activeItem == 0 ? Active : InActive} onClick={()=>PtNavigator(0)}> Cargobike</p></PtButton>
                    <PtButton> <p style={activeItem == 1 ? Active : InActive} onClick={()=>PtNavigator(1)}> Agave</p></PtButton>
                    <PtButton> <p style={activeItem == 2 ? Active : InActive} onClick={()=>PtNavigator(2)}> Digitale Starter</p></PtButton>
                    <PtButton> <p style={activeItem == 3 ? Active : InActive}onClick={()=>PtNavigator(3)}> Kasten passer</p></PtButton>
                  </div>
                </div>
                <div className="
                  sm:float-right sm:static sm:mr-10
                  md:mt-12
                  xl:bottom-28 xl:left-3 xl:w-fit xl:absolute">
                  <div className="aspect-square flex flex-shrink-1 overflow-hidden rounded-full border-4 border-dashed border-highlight1 bg-white">
                    <img src={frame2}/>
                  </div>
                </div>
              </div>

          </div>
          

            {/* TEXT GRID2*/}
            <div id="PortContentScroll" className="
              xl:h-96 xl:col-span-5 xl:w-full" >
              <div className="xl:grid xl:grid-cols-3 ">
                <div className="xl:col-span-2 xl:w-full">
                    <div className="">
                      <div id="HeroImg" className="bg-cover h-64 relative xl:h-32" style={{backgroundImage: `url(${heroImg[activeItem]})`}}>
                        <div className="bg-gradient-to-b to-transparent from-light2 h-12 xl:bg-none"/>
                        <div className="h-12 xl:h-0"></div> {/* Menu buffer */}
                          <h2 className="text-5xl text-right pb-6 text-dark2 font-bold
                            xl:text-left xl:pl-6 z-10 bottom-0 absolute w-full pt-6 pt-text-outline
                          " id="PortContent">
                          {portfolioItems[activeItem].title}
                        </h2>
                      </div>
                    </div>
                    
                    <div className="bg-contrast text-center xl:1/3 xl:pl-6">
                      {portfolioItems[activeItem].pitch.map((pitch,key) => 
                        <p key={key} className="xl:pt-10 p-3 pt-10 pb-10 text-dark2 text-2xl font-handwrite font-semibold" id="PortContent"> 
                          {portfolioItems[activeItem].pitch[key]} 
                        </p>
                      )}
                    </div>

                    <div className="xl:columns-2 xl:pl-6 xl:pt-6">
                      <div className="pt-6 xl:b-20 xl:pt-0">
                        {portfolioItems[activeItem].descr.map((descr,key) => 
                          <p key={key} className="font-medium text-base pl-6 pr-6 pt-3 text-dark1
                          xl:pl-0 xl:pr-0 xl:pt-0 xl:pb-6 xl:text-lg" id="PortContent"> 
                            {portfolioItems[activeItem].descr[key]} 
                          </p>
                        )}
                      </div>
                    </div>
                </div>

                {/* IMAGES GRID3*/}      
                <div className="pt-20
                  md:pt-0
                  xl:col-span-1 xl:h-screen xl:h-screen xl:overflow-y-scroll xl:relative xl:bottom-0"> 
                  <div className="h-16 xl:hidden">
                    <img className="mx-auto pt-10" src={SwipeImg}/>
                  </div>
                  <div className="Album overflow-auto xl:overflow-visible">
                    <div className="flex 
                      xl:block xl:overflow-auto xl:ml-0 xl:mt-0">
                      {portfolioItems[activeItem].album.map((image,key) => 
                        <div key={key} id={key} className="portImage flex-shrink-0
                            xl:h-[90%] xl:overflow-y-auto xl:overflow-x-hidden">
                          <img className="mx-auto mt-20 -pr-3 scale-x-75 scale-y-75 skew-y-2
                            xl:skew-y-0 xl:scale-x-100 xl:scale-y-100 xl:mx-auto xl:mt-5 xl:w-[100%] xl:w-auto" src={image}/>
                        </div>
                      )}
                    </div>
                  </div>
                </div> 

              </div>
            </div>


      </div>{/* PAGE CONTAINER */}
    </div>
  );
}

export default Portfolio;
