import Animation from "../components/Animation.js";
import { gsap } from "gsap";
import { useEffect, useRef, forwardRef } from "react";
import styled from "styled-components";

import Michelles from '../images/homepage/svg/michelles.svg';
import Portfolio from '../images/homepage/svg/portfolio.svg';
import Hoekje from '../images/homepage/svg/hoekjevhinternet.svg';
import Bling from '../images/homepage/svg/bling.svg';

import backgroundimg from '../images/background/bg.png';
//Af 

const Bgimg = styled.div`
  background-image: url(${backgroundimg});
  height: 100vh;
  width: 100vw;
  z-index: -800;
  backgroundAttachment: fixed
  backgroundPosition: top;
`

function WelcomePage() {
  const welcomePage = useRef();

  useEffect(() => {
    
    let ctx = gsap.context(() => {
      
      gsap.fromTo(".firstname", {opacity:0},{rotation: -20, duration: .8, delay: 0.3, opacity: 1});
      gsap.fromTo(".lastname", {opacity:0}, {rotation: -20,duration: .8, delay: 0.6, opacity: 1});
      gsap.fromTo(".bling", {opacity:0, rotation: -20}, {duration: 0.5, delay: 1.3, opacity: 1});
      
    }, welcomePage); // <- IMPORTANT! Scopes selector text
    
    return () => ctx.revert(); // cleanup
    
  }, []);

  return (
    <div ref={welcomePage} className="WelcomePage h-screen w-screen overflow-y-hidden overflow-x-hidden">
      <Bgimg>
      <div className="
        flex flex-wrap justify-between grid grid-cols-1
        lg:grid-cols-2 lg:w-2/3 m-auto">

          <div className="
            mt-20 ml-12 w-2/3
            sm:mt-30 sm:mx-auto
            lg:mt-30 lg:ml">
              <img className="firstname shrink-0" src={Michelles}/>
              <div className="relative">
                <img className="lastname" src={Hoekje}/>
                <img className="bling absolute -top-2" src={Bling}/>
              </div>
            </div>

          <div className="pt-6 w-fit sm:mx-auto sm:mt-10 shrink-0">
            <div className="lg:absolute lg:bottom-28 lg:right-[25%]">
              <div className="mx-auto">
                <div className="aspect-square h-[500] overflow-hidden rounded-full border-4 border-dashed border-highlight1 ">
                <Animation toPlay="Welcome"/>
                </div>
              </div>
            </div>
          </div>
          
      </div>
      </Bgimg>
    </div>
  );
}

export default WelcomePage;
