import { gsap } from "gsap";
import { useState, useEffect, useRef, useLayoutEffect } from "react";
import { Link, useNavigate} from 'react-router-dom';
import MenuIcon from '../images/menubutton.svg';

//Component. Refactor candidate
function Navbar() {
  const navibar = useRef();
  const navibar2 = useRef();
  const totalBar = useRef();
  const navigate = useNavigate();

  //Phone Nav
  const[menuOpen, setMenuOpen] = useState(0);

  function toggleNav(){
    setMenuOpen(!menuOpen);
  }

  function OpenMenu(){
    let ctx2 = gsap.context(() => {
      gsap.fromTo(".navItems", {opacity:0, display:'block'},{duration: .3, opacity: 1});
      gsap.fromTo(".navItem", {opacity:0, y:-50, display:'block'}, {duration: .15, y:0, stagger:0.05, opacity: 1});
    }, totalBar); // <- IMPORTANT! Scopes selector text
  }

  function CloseMenu(){
    let ctx3 = gsap.context(() => {
      gsap.fromTo(".navItems", {opacity:1, display:'block'},{duration: .2, opacity: 0});
      gsap.fromTo(".navItem", {opacity:1, y:0, display:'block'}, {duration: .2, y:+50, stagger:0.1, opacity: 0});
      //Disable
      setTimeout(() => {
      gsap.fromTo(".navItems", {opacity:0},{display:'none',});
      gsap.fromTo(".navItem", {opacity:0}, {display:'none'});
      }, 200);
    }, totalBar); // <- IMPORTANT! Scopes selector text


  }

  useEffect(()=>{
    if(menuOpen){
      OpenMenu();
    }else{
      CloseMenu();
    }
  }, [menuOpen]);

  //Desktop/Tablet Nav
  const [location, setLocation] = useState(0);
  const [oldxcoor, setOldxcoord] = useState(0);
  const [newxcoord, setNewxcoord] = useState(0);

  useEffect(()=>{
    switch(location){
      case "Home":
        setNewxcoord(0);
        break;
      case "Portfolio":
        setNewxcoord(1);
        break;
      case "Over Mij":
        setNewxcoord(2);
        break;
      case "Vaardigheden":
        setNewxcoord(3);
        break;
      case "Contact":
        setNewxcoord(4);
        break;
    }
  }, [location]);

  useEffect(()=>{
    let ctx = gsap.context(() => {
      //gsap.fromTo(".navbutton", {x:oldxcoor},{x: ((((window.innerWidth/3)*2)/5)* newxcoord)-5});
      gsap.fromTo(".navbutton", {x:oldxcoor},{x: ((((window.innerWidth/3)*2)/5)* newxcoord)-5});
    }, navibar); 
    setOldxcoord((((window.innerWidth/3)*2)/5)* newxcoord);
  }, [newxcoord]);

  return (
  <div className="totalBar Navbar z-50" ref={totalBar}>
    {/* DESKTOP */}
    <div ref={navibar} className="Navbar lg:fixed bottom-0 z-50 w-screen">
      <div className="z-40 hidden 
        bg-dark1 p-2 w-screen overflow-x-hidden h-2
        lg:bottom-0 lg:absolute lg:h-24 lg:overflow-hidden lg:block 
      ">
        <div className="h-screen lg:h-fit">
          <div className="lg:block">
            <div className="
              grid-cols-5 pt-3 lg:text-3xl md:text-1xl sm:text-1xl
              ">
              <button onClick={()=>setLocation("/")} className="z-30 w-48 pt-8 lg:pt-0">
                <h1 className="text-light font-semibold tracking-full mx-auto ">
                <Link to="/">Home</Link>
                </h1>
              </button>

              <button onClick={()=>setLocation("Portfolio")} className="z-30 w-48 pt-8 lg:pt-0">
                <h1 className="text-light font-semibold tracking-full mx-auto">
                <Link to="/Portfolio">Portfolio</Link>
                </h1>
              </button>

              <button onClick={()=>setLocation("Over Mij")}className=" z-30 w-48 pt-8 lg:pt-0">
                <h1 className="text-light font-semibold tracking-full mx-auto">
                <Link to="/OverMij">Over Mij</Link>
                </h1>
              </button>

              <button className=" z-30 w-48 pt-8 lg:pt-0">
                <h1 onClick={()=>setLocation("Vaardigheden")} className="text-light font-semibold tracking-full mx-auto">
                <Link to="/Vaardigheden">Vaardigheden</Link>
                </h1>
              </button>
              <div className="navbutton bg-highlight2 h-18 w-48 rounded-lg "/>
            </div>
          </div>
        </div>

     
      </div>
    </div>

     {/* MOBILE MENU */}
    <div className="fixed z-50">
      <div ref={navibar2} className="p-2 inline-block lg:hidden">
        <button className="bg-highlight2 p-4 rounded-full" onClick={()=>toggleNav()}><img src={MenuIcon} className="h-12"/></button>
      </div>

      <div className="navItems lg:hidden bg-dark1 w-screen h-screen top-0 absolute">
        <div className="
          text-center pt-12 grid mx-auto w-1/2">

          <button onClick={()=>{CloseMenu(); setMenuOpen(0)}} className="navItem z-30 w-48 pt-8 lg:pt-0">
            <h1 onClick={()=>setLocation("/")} className="text-3xl text-highlight1 font-semibold tracking-full mx-auto lg:no-underline">
            <Link to="/">Home</Link>
            </h1>
          </button>

          <button onClick={()=>{CloseMenu(); setMenuOpen(0)}} className="navItem z-30 w-48 pt-8 lg:pt-0">
            <h1 onClick={()=>setLocation("Portfolio")} className="text-3xl text-highlight1 font-semibold tracking-full mx-auto lg:no-underline">
            <Link to="/Portfolio">Portfolio</Link>
            </h1>
          </button>

          <button onClick={()=>{CloseMenu()}} className="navItem z-30 w-48 pt-8 lg:pt-0">
            <h1 onClick={()=>setLocation("Over Mij")} className="text-3xl text-highlight1 font-semibold tracking-full mx-auto lg:no-underline">
            <Link to="/OverMij">Over Mij</Link>
            </h1>
          </button>

          <button onClick={()=>{CloseMenu()}} className="navItem z-30 w-48 pt-8 lg:pt-0">
            <h1 onClick={()=>setLocation("Vaardigheden")} className="text-3xl text-highlight1 font-semibold tracking-full mx-auto lg:no-underline">
            <Link to="/Vaardigheden">Vaardigheden</Link>
            </h1>
          </button>

          <button onClick={()=>{CloseMenu()}} className="navItem z-30 w-48 pt-8 lg:pt-0">
            <h1 className="text-3xl text-highlight1  font-semibold tracking-full mx-auto lg:no-underline">
              X
            </h1>
          </button>
        </div>
      </div>
    </div>
  </div>
  );
}

export default Navbar;